.header {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  background-color: white;
  height: 48px;
  .header-title {
    margin-left: 1%;
    font-family: "NanumBarunpenB";
    font-size: 1.5rem;
    color: black;
    text-decoration: none;
  }
  .header-icons {
    margin-left: auto;
    margin-right: 1%;
  }
  .menu-button {
    background-color: white;
    margin-left: 1%;
  }
  .github-button {
    background-color: white;
  }
}
