.nvg-wrapper {
  position: fixed;
  left: 0;
  top: 0;
  .nvg-open,
  .nvg-close {
    position: fixed;
    left: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 300px;
    height: 1680px;
    background-color: #f8f8f8;
    border-right: 1px #00000029 solid;
  }
  .nvg-open {
    animation: slide-in-left 0.5s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
    @keyframes slide-in-left {
      0% {
        transform: translateX(-1000px);
        opacity: 0;
      }
      100% {
        transform: translateX(0);
        opacity: 1;
      }
    }
  }
  .nvg-close {
    animation: slide-out-left 0.5s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
    @keyframes slide-out-left {
      0% {
        transform: translateX(0);
        opacity: 1;
      }
      100% {
        transform: translateX(-1000px);
        opacity: 0;
      }
    }
  }
  .nvg-icon-svg {
    margin-top: 5%;
    width: 134px;
    height: 134px;
    background-image: url("./img/icon.svg");
    background-color: white;
    background-size: 75%;
    background-repeat: no-repeat;
    background-position-x: center;
    background-position-y: center;
    border-radius: 50%;
    border: 1px #00000029 solid;
  }
  .nvg-name {
    margin-top: 4%;
    font-size: 2rem;
    font-family: "NanumBarunpenB";
  }
  .nvg-info {
    margin-top: 2%;
    font-size: 1.2rem;
    font-family: "NanumBarunpenB";
  }
  .nvg-divider {
    margin: 5% 0;
    width: 160px;
    border-bottom: 2px solid #00000029;
  }
  .nvg-menu {
    margin: 3% 0;
    font-size: 1.2rem;
    font-family: "font/NanumSquareB";
  }
}
