.container {
  display: flex;
  align-items: flex-start;
  justify-content: flex-end;
  width: 100%;
  height: 1680px;
  background-color: #fafafa;
  .container-h {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    width: 80%;
    min-width: 1024px;
    height: 100%;
    background-color: #fafafa;
    .first-title {
      font-size: 3rem;
      font-family: "font/NanumSquareB";
    }
    .second-title {
      margin-top: 2%;
      margin-left: 0.5%;
      font-size: 2rem;
      font-family: "font/NanumSquareR";
      text-decoration: none;
      color: black;
    }
    .second-title-link {
      font-size: 1.5rem;
      font-family: "font/NanumSquareR";
      text-decoration: none;
      color: black;
    }
    a:hover {
      color: #6c7a89;
    }
  }
}
