@import "./header";
@import "./container";
@import "./color";
@import "./navigation";
@import "./_homeContents";
@import "./thumbnailContainer";

root {
  font-size: 12px;
}

@font-face {
  font-family: "NanumBarunpenB";
  src: url("font/NanumBarunpenB.ttf") format("truetype");
}

@font-face {
  font-family: "font/NanumSquareR";
  src: url("font/NanumSquareR.ttf") format("truetype");
}

@font-face {
  font-family: "font/NanumSquareB";
  src: url("font/NanumSquareB.ttf") format("truetype");
}

button {
  border: none !important;
  border-radius: 8px;
  cursor: pointer;
}
button:hover {
  background-color: #e0e7e9 !important;
  background-clip: border-box;
}
button:focus,
button:active {
  border: none !important;
  outline: none !important;
}

* {
  margin: 0;
  padding: 0;
}
